          courseID: "1",
        },
        {
          courseName: "台灣廠區承攬商入場危害告知課程/台中廠次",
          courseID: "2",
        },
      ],
      queryDataList: [
        {
          index: 0,
          courseID: "0",
          applyNumber: "00003456789",
          project: {
            projectNo: "000000585423",
            projectName: "111年群創光電消防檢修工程",
            applyDate: "2022/01/19",
            workDate: "2022/02/01",
            workEndDate: "2023/01/31",
          },
          projectName: "000000585423/111年群創光電消防檢修工程",
          date: "2022/01/19 (2022/02/01 ~ 2023/01/31)",
          supervisor: {
            pName: "安全人員姓名",
            pID: "A1234567890",
          },
          supervisorName: "安全人員姓名(A1234567890)",
        },
        {
          index: 1,
          courseID: "0",
          applyNumber: "00003456789",
          project: {
            projectNo: "000000585423",
            projectName: "111年群創光電消防檢修工程",
            applyDate: "2022/01/19",
            workDate: "2022/02/01",
            workEndDate: "2023/01/31",
          },
          projectName: "000000585423/111年群創光電消防檢修工程",
          date: "2022/01/19 (2022/02/01 ~ 2023/01/31)",
          supervisor: {
            pName: "安全人員姓名",
            pID: "A1234567890",
          },
          supervisorName: "安全人員姓名(A1234567890)",
        },
        {
          index: 2,
          courseID: "0",
          applyNumber: "00003456789",
          project: {
            projectNo: "000000585423",
            projectName: "111年群創光電消防檢修工程",
            applyDate: "2022/01/19",
            workDate: "2022/02/01",
            workEndDate: "2023/01/31",
          },
          projectName: "000000585423/111年群創光電消防檢修工程",
          date: "2022/01/19 (2022/02/01 ~ 2023/01/31)",
          supervisor: {
            pName: "安全人員姓名",
            pID: "A1234567890",
          },
          supervisorName: "安全人員姓名(A1234567890)",
        },
        {
          index: 3,
          courseID: "0",
          applyNumber: "00003456789",
          project: {
            projectNo: "000000585423",
            projectName: "111年群創光電消防檢修工程",
            applyDate: "2022/01/19",
            workDate: "2022/02/01",
            workEndDate: "2023/01/31",
          },
          projectName: "000000585423/111年群創光電消防檢修工程",
          date: "2022/01/19 (2022/02/01 ~ 2023/01/31)",
          supervisor: {
            pName: "安全人員姓名",
            pID: "A1234567890",
          },
          supervisorName: "安全人員姓名(A1234567890)",
        },
      ],
      queryData: [],
      supervisorList: [
        {
          id: 0,
          pName: "安全人員姓名",
          urgentTel: "0912345678",
          pID: "A1234567890",
        },
        {
          id: 1,
          pName: "安全人員姓名",
          urgentTel: "0912345678",
          pID: "A1234567890",
        },
        {
          id: 2,
          pName: "安全人員姓名",
          urgentTel: "0912345678",
          pID: "A1234567890",
        },
        {
          id: 3,
          pName: "安全人員姓名",
          urgentTel: "0912345678",
          pID: "A1234567890",
        },
      ],
      formQuery: {
        course: {}, //承攬商入廠施工群組
        applyNumber: "", //承攬商申請單號
      },
      formSend: {
        selectedData: {}, //已選擇的施工單號
        changeSupervisor: {}, //欲變更的安全人員資料
      },
      rulesQuery: {
        course: [{ required: true, message: "必填", trigger: "change" }],
        applyNumber: [{ required: true, message: "必填", trigger: "change" }],
      },
      rulesSend: {
        changeSupervisor: [
          { required: true, message: "必填", trigger: "change" },
        ],
      },
      tableColumns: [
        {
          id: "applyNumber",
          label: "申請單號",
        },
        {
          id: "projectName",
          label: "工程編號/工程名稱",
          width: "250",
        },
        {
          id: "date",
          label: "建立日期(開始~結束日)",
        },
        {
          id: "supervisorName",
          label: "廠商施工負責人",
        },
      ],
    };
  },
  computed: {
    dataList: {
      get() {
        var vm = this;
        return vm.queryDataList.filter((x) => {
          return (
            x.courseID == vm.formQuery.course.courseID &&
            vm.formQuery.applyNumber != "" &&
            x.applyNumber.indexOf(vm.formQuery.applyNumber) >= 0
          );
        });
      },
    },
  },
  methods: {
    changeSelection(item) {
      var vm = this;
      const data = {
        selected: [],
      };
      data.selected.push(item);
      data.selected = data.selected.flat();
      vm.selectedData = data.selected;
    },
    changePage(page) {
      var vm = this;
      vm.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsListName: "projectList",
        paramsDataName: "projectListData",
      });
    },
    onQueryClick() {
      var vm = this;
      vm.queryData = [];
      if (vm.formQuery.course.courseID == null) {
        vm.$message({
          type: "warning",
          showClose: true,
          message: "請先選取承攬商入廠施工群組!",
        });
        return;
      }
      if (vm.formQuery.applyNumber == null || vm.formQuery.applyNumber == "") {
        vm.$message({
          type: "warning",
          showClose: true,
          message: "請輸入申請單號條件!",
        });
        return;
      }
      vm.queryData = JSON.parse(JSON.stringify(vm.dataList));
      if (vm.queryData.length == 0) {
        vm.$message({
          type: "warning",
          showClose: true,
          message: "此條件查無資料!",
        });
        return;
      }
    },
    submitForm() {
      var vm = this;
      if (vm.formSend.changeSupervisor.pID == null) {
        vm.$message({
          type: "warning",
          showClose: true,
          message: "所選人員空白,請選擇人員!",
        });
        return;
      }

      vm.formSend.selectedData = vm.selectedData;
      console.log("預計將發送的資料");
      console.log(vm.formSend);
      vm.$message({
        type: "success",
        showClose: true,
        message: "檢查完成，資料可以發送",
      });
    },
  },
  async mounted() {
    var vm = this;
    //頁面載入之後，打開上方 Header 與 左側 SideBar
    vm.$store.commit("HEADER_HANDLER", true);
    vm.$store.commit("BREADCRUMB_HANDLER", true);
    vm.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

